  import "./common/theme-section";

  register("featured-product", {
    onLoad: function () {
      if (document.getElementsByClassName("productSlider").length > 0) {
        var slider = tns({
          container: ".productSlider",
          items: 1,
          slideBy: 1,
          mouseDrag: true,
          gutter: 30,
          swipeAngle: false,
          nav: true,
          navPosition: "bottom",
          speed: 400,
          controlsContainer: "#controls",
          prevButton: ".arrow__left",
          nextButton: ".arrow__right",
          responsive: {
            768: {
              items: 2,
            },
            1024: {
              nav: false,
            },
            1280: {
              items: 3,
            },
            1500: {
              items: 4,
            },
          },
        });
      }
    },
    onUnload: function () {},
    onSelect: function () {},
    onDeselect: function () {},
    onBlockSelect: function (e) {},
    onBlockDeselect: function (e) {},
  });
  
  load("*");
  
  register("product-review", {
    onLoad: function () {
      const jsonDataProductReview = JSON.parse(
        this.container.querySelector("#reviewSlider").innerText
      );
      const num = jsonDataProductReview.number;
      console.log(jsonDataProductReview);
      var Customerslider = tns({
        container: ".Slider",
        items: 1,
        slideBy: 1,
        mouseDrag: true,
        swipeAngle: false,
        nav: true,
        navPosition: "bottom",
        controlsContainer: "#controls-customer",
        prevButton: ".prevButton",
        nextButton: ".nextButton",
        speed: 400,
        responsive: {
          400: {
            items: 1,
          },
          640: {
            edgePadding: 20,
            gutter: 20,
            items: 2,
          },
          700: {
            gutter: 30,
          },
          900: {
            items: num,
          },
        },
      });
    },
    onchange: function () {},
    onUnload: function () {},
    onSelect: function () {},
    onDeselect: function () {},
    onBlockSelect: function (e) {},
    onBlockDeselect: function (e) {},
  });
  
  load("*");
  
  register("single-banner", {
    onLoad: function () {
      const jsonData = JSON.parse(
        this.container.querySelector("#btnBanner").innerText
      );
      console.log(jsonData);
      const color = jsonData.color;
      const blockId = jsonData.blockId;
      function shadeColor1(color, percent) {
        // deprecated. See below.
        var num = parseInt(color.slice(1), 16),
          amt = Math.round(2.55 * percent),
          R = (num >> 16) + amt,
          G = ((num >> 8) & 0x00ff) + amt,
          B = (num & 0x0000ff) + amt;
        return (
          "#" +
          (
            0x1000000 +
            (R < 255 ? (R < 1 ? 0 : R) : 255) * 0x10000 +
            (G < 255 ? (G < 1 ? 0 : G) : 255) * 0x100 +
            (B < 255 ? (B < 1 ? 0 : B) : 255)
          )
            .toString(16)
            .slice(1)
        );
      }
      if (color) {
        let btn = document.querySelector(`.singleBanner__btnBlock-${blockId}`);
        let percent = 30;
        var gradientColor = `linear-gradient(90deg, ${shadeColor1(
          color,
          percent * -1
        )}, ${color} 50% , ${shadeColor1(color, percent)} 100%)`;
        btn.style.background = gradientColor;
      }
      console.log(color);
      document.addEventListener("DOMContentLoaded", function () {});
    },
    onchange: function () {},
    onUnload: function () {},
    onSelect: function () {},
    onDeselect: function () {},
    onBlockSelect: function (e) {},
    onBlockDeselect: function (e) {},
  });
  
  load("*");